@import 'components/_components'
@import 'pages/_pages'
//DONT REPEAT YOURSELF

@import ./mfv
    
//Base Styles
*
    margin: 0
    padding: 0
    font-size: 62.5%
    font-family: 'Muli', sans-serif
    overflow-x: hidden

h1,h2,h3,h4,h5,h6,p,svg,i,span,button,a
    color: $gray
    font-weight: 300
a
    text-decoration: none
section,html,body
    height: 100%
section
    margin: auto

img
    height: 100%
    width: 100%

div.btn-box
    width: 100vw
button.MuiButton-containedPrimary, a.MuiButton-containedPrimary
    background-color: $flower !important
    border-radius: unset
    height: 10vh
    width: 100%
    @media screen and ( min-width: 600px )
        width: 65%
        margin: auto
        border-radius: 10rem
        height: 6.5rem
    a.MuiButtonBase-root
        text-transform: unset !important
    span.MuiButton-label
        color: $white
        font-size: 1.5rem
        text-shadow: 0.4px 0.4px darken($flower, 25%)
        text-transform: unset !important
        @media screen and ( min-width: 600px )
            font-size: 2.5rem
            font-style: italic

div.landing-page
    max-height: 90vh
    height: 90vh
    @include wrap(column)
    justify-content: space-between
div.landing-img-box
    height: 60vh
    @include flexCenter()
    img
        height: 40%
        @media screen and ( min-width: 600px )
            height: 50%
            width: 75%

div.content-box
    @include wrap(column)
    text-align: center
    width: 80%
    h1.mvp
        font-size: 3rem
        font-weight: 900
        padding-top: 2rem
    h1.content
        font-size: 1.7rem
        text-align: left
        font-weight: 900
        font-style: italic
    h2.content
        font-size: 1.7rem
    h3.content
        font-size: 1.5rem
    p.content
        font-size: 1.2rem
        padding: .5rem 0
        line-height: 1.7rem
        color: black
    p.mvp
        text-align: right
        font-size: 1.2rem
        color: black
    
   
    
    
